import React, { Component } from 'react';
import './style.css';
import Navbar from '../../components/navigation'
import MoviePane from '../../components/MoviePane';
import MovieAPI from '../../utils/MovieAPI';
import ReactPaginate from 'react-paginate';

class Search extends Component {
	constructor() {
		super();

		this.state = {
			movieInput: '',
			movies: [],
			pages: 1,
			page: 0
		}

		this.handleInputChange = this.handleInputChange.bind(this);
		this.handleMovieClick = this.handleMovieClick.bind(this);
		this.handleSearch = this.handleSearch.bind(this);
		this.handlePageChange = this.handlePageChange.bind(this);
	}

	handlePageChange(data) {
		this.setState({
			page: data.selected,
		}, () => {
			MovieAPI.searchMovie(this.state.lastSearch, this.state.page)
			.then(movies => {
				this.setState({
					movies: movies.data.results,
					pages: movies.data.total_pages,
					movieInput: ''
				})
			})
		})
	}

	handleMovieClick(id, movie) {
		this.props.history.push({
			pathname: `/movie/${id}`,
			movie: movie
		})
	}

	handleInputChange(e) {
		this.setState({
			movieInput: e.target.value
		})
	}

	handleSearch() {
		this.setState({
			lastSearch: this.state.movieInput
		}, () => {
			MovieAPI.searchMovie(this.state.lastSearch, this.state.page)
			.then(movies => {
				console.log(movies)
				this.setState({
					movies: movies.data.results,
					pages: movies.data.total_pages,
					movieInput: ''
				})
			})
		})
	}

	render() {
		const movies = this.state.movies
		.map((movie, index) => (
			<MoviePane
				title={movie.title}
				key={index}
				rating={movie.vote_average}
				showInfo={() => {this.handleMovieClick(movie.id, movie)}}
				backgroundImage={movie.poster_path === null ? 'http://placehold.it/178x278' : `https://image.tmdb.org/t/p/w342${movie.poster_path}`}/>
		));

		const visibility = this.state.movies.length === 0 ? 'hidden' : 'visible';
		const opacity = this.state.movies.length === 0 ? 0 : 1;

		return (
			<div className="search">
				<Navbar />
				<div className="row">
					<div className="col-lg-6 col-lg-offset-3">
						<div className="input-group">
							<input type="text" className="form-control" placeholder="Search for a movie..." value={this.state.movieInput} onChange={this.handleInputChange} />
							<span className="input-group-btn">
								<button className="btn btn-primary" type="button" onClick={this.handleSearch}>Search</button>
							</span>
						</div>
					</div>
				</div>

				<div className="row">
					<div className="container movies" style={
						{
							visibility: visibility,
							opacity: opacity
						}}>
						<div className="paginateWrap">
							<ReactPaginate
								previousLabel={"Previous"}
								nextLabel={"Next"}
								breakLabel={<span>...</span>}
								breakClassName={"break-me"}
								forcePage={this.state.page}
								onPageChange={this.handlePageChange}
								pageCount={this.state.pages}
								marginPagesDisplayed={5}
				                pageRangeDisplayed={9}
				                containerClassName={"pagination"}
				               	subContainerClassName={"pages pagination"}
				               	activeClassName={"active"} />
						</div>

						{movies}

						<div className="paginateWrap">
							<ReactPaginate
								previousLabel={"Previous"}
								nextLabel={"Next"}
								breakLabel={<span>...</span>}
								breakClassName={"break-me"}
								onPageChange={this.handlePageChange}
								pageCount={this.state.pages}
								marginPagesDisplayed={5}
			                    pageRangeDisplayed={9}
			                    containerClassName={"pagination"}
			                   	subContainerClassName={"pages pagination"}
			                   	activeClassName={"active"}
			                   	forcePage={this.state.page} />
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Search;


