import React, { Component } from 'react';
import './style.css';
import MoviePane from '../../components/MoviePane';
import MovieAPI from '../../utils/MovieAPI';
import ReactPaginate from 'react-paginate';
import Navbar from '../../components/navigation'

class NowPlaying extends Component {
	constructor() {
		super();

		this.state = {
			movies: [],
			pages: 1,
			page: 0
		}

		this.handlePageChange = this.handlePageChange.bind(this);
		this.handleMovieClick = this.handleMovieClick.bind(this);
	}

	handlePageChange(data) {
		this.setState({
			page: data.selected
		}, () => {
			MovieAPI.getNowPlaying(this.state.page)
			.then(movies => {
				this.setState({
					movies: movies.data.results
				})
			})
		})
	}

	handleMovieClick(id, movie) {
		this.props.history.push({
			pathname: `/movie/${id}`,
			movie: movie
		})
	}


	componentWillMount() {
		MovieAPI.getNowPlaying(this.state.page)
		.then(movies => {
			this.setState({
				movies: movies.data.results,
				pages: movies.data.total_pages
			})
		})
	}

	render() {

		const movies = this.state.movies
		.map((movie, index) => (
			<MoviePane
				title={movie.title}
				key={index}
				rating={movie.vote_average}
				showInfo={() => {this.handleMovieClick(movie.id, movie)}}
				backgroundImage={movie.poster_path === null ? 'http://placehold.it/178x278' : `https://image.tmdb.org/t/p/w342${movie.poster_path}`}/>
		));
		return (
			<div>
				<Navbar />
				<div className="container" style={{marginTop:'50px'}}>
					<div className="paginateWrap">
						<ReactPaginate
							previousLabel={"Previous"}
							nextLabel={"Next"}
							breakLabel={<span>...</span>}
							breakClassName={"break-me"}
							forcePage={this.state.page}
							onPageChange={this.handlePageChange}
							pageCount={this.state.pages}
							marginPagesDisplayed={5}
			                pageRangeDisplayed={9}
			                containerClassName={"pagination"}
			               	subContainerClassName={"pages pagination"}
			               	activeClassName={"active"} />
					</div>

					{movies}

					<div className="paginateWrap">
						<ReactPaginate
							previousLabel={"Previous"}
							nextLabel={"Next"}
							breakLabel={<a href="">...</a>}
							breakClassName={"break-me"}
							onPageChange={this.handlePageChange}
							pageCount={this.state.pages}
							marginPagesDisplayed={5}
		                    pageRangeDisplayed={9}
		                    containerClassName={"pagination"}
		                   	subContainerClassName={"pages pagination"}
		                   	activeClassName={"active"}
		                   	forcePage={this.state.page} />
					</div>
				</div>
			</div>
		);
	}
}

export default NowPlaying;


