import React, { Component } from 'react';
import './style.css';
import Navbar from '../../components/navigation'
import MovieAPI from '../../utils/MovieAPI';
import LazyLoad from 'react-lazy-load';

class MovieInfo extends Component {
	constructor(props) {
		super(props);

		this.state = {
			movieInfo: {},
		}
	}

	componentWillMount() {
		const { match } = this.props;
		let movieID = match.params.name;
		MovieAPI.getMovie(movieID)
		.then(response => {
			if (response.status_code === 34) {
			} else {
				this.setState({
					movieInfo: response.data,
					poster: response.data.poster_path,
					backdrop: response.data.backdrop_path
				})
			}
		})
	}

	render(props) {
		const poster = this.state.poster === undefined ? 'http://placehold.it/185x278' : 'https://image.tmdb.org/t/p/w185' + this.state.poster;
		const backdrop = this.state.backdrop === undefined ? 'http://placehold.it/1920x1080' : 'https://image.tmdb.org/t/p/original' + this.state.backdrop;

		const backdropStyle = {
			backgroundImage: `url(${backdrop})`
		}
		return (
			<LazyLoad>
				<div className="movieInfo" style={{height: '100%'}}>
					<Navbar />
						<div className="top" style={backdropStyle}></div>
					<div className="container movie">
						<div className="poster">
								<img src={poster} alt=""/>
						</div>
						<div className="header">
							<h1 className="title">{this.state.movieInfo.title}</h1>
							<h4>Released: <b>{formatDate(this.state.movieInfo.release_date)}</b></h4>
							<h4>Rating: <b>{this.state.movieInfo.vote_average} / 10</b></h4>

							<p>{this.state.movieInfo.overview}</p>
						</div>

					</div>
				</div>
			</LazyLoad>
		);
	}
}

function formatDate(datestr) {
  var monthNames = [
    "January", "February", "March",
    "April", "May", "June", "July",
    "August", "September", "October",
    "November", "December"
  ];

  let date = new Date(datestr);

  var day = date.getDate();
  var monthIndex = date.getMonth();
  var year = date.getFullYear();

  return monthNames[monthIndex] + ' ' + day + ', ' + year;
}

export default MovieInfo;


