import React from 'react';
import './style.css';
import Navbar from '../../components/navigation'

const Home = () => (
	<div style={{paddingTop: '71px'}}>
		<Navbar />
		<div className="container">
			<div className="content">
				<h2>Welcome!</h2>
				<h3>This is <b><a href="http://malikbrowne.com">Malik Browne's</a></b> submission for the Konica Minolta Interview Assessment.</h3>
				<p>Below, you will find a detailed description of my submission:</p>

				<p>The Movie App is API driven, pulling data from three different endpoints provided by the TMDB API:</p>
				<ol>
					<li>
						<a href="https://developers.themoviedb.org/3/movies" target="_blank" rel="noopener noreferrer">Get Details</a>
					</li>
					<li>
						<a href="https://developers.themoviedb.org/3/movies/get-now-playing" target="_blank" rel="noopener noreferrer">Get Now Playing</a>
					</li>
					<li>
						<a href="https://developers.themoviedb.org/3/search/search-movies" target="_blank" rel="noopener noreferrer">Search Movie</a>
					</li>
				</ol>

				<p>The application was built using ReactJS and several NPM packages:</p>
				<ul>
					<li>
						<a href="https://github.com/mzabriskie/axios" target="_blank" rel="noopener noreferrer">Axios</a>
					</li>
					<li>
						<a href="https://github.com/facebook/react" target="_blank" rel="noopener noreferrer">React</a>
					</li>
					<li>
						<a href="https://github.com/react-bootstrap/react-bootstrap" target="_blank" rel="noopener noreferrer">React-Bootstrap</a>
					</li>
					<li>
						<a href="https://github.com/loktar00/react-lazy-load" target="_blank" rel="noopener noreferrer">React-Lazy-Load</a>
					</li>
					<li>
						<a href="https://github.com/AdeleD/react-paginate" target="_blank" rel="noopener noreferrer">React-Paginate</a>
					</li>
					<li>
						<a href="https://github.com/reacttraining/react-router" target="_blank" rel="noopener noreferrer">React-Router</a>
					</li>
				</ul>

				<p>In order to save time (which allowed me to spend more time on functionality/user experience), the project was bootstrapped with <a href="https://github.com/facebookincubator/create-react-app">create-react-app</a>!</p>

				<p>Hope you like my submission!</p>
			</div>
		</div>
	</div>
);

export default Home;


