import React from 'react';
import './style.css';
import MoreInfoImg from './external-link-symbol.png';
import PropTypes from 'prop-types'

const MoviePane = (props) => {
	return (
			<div className="paneWrapper" onClick={props.showInfo}>
				<div className="pane" style={{backgroundImage: `url(${props.backgroundImage})`}}>
					<div className="overlay">
						<p className="title">{props.title}</p>
						<p>Rating: {props.rating} / 10</p>
						<img src={MoreInfoImg} alt=""/>
					</div>
				</div>
			</div>
	);
}


MoviePane.propTypes = {
	title: PropTypes.string.isRequired,
	backgroundImage: PropTypes.string,
	rating: PropTypes.number
}

export default MoviePane;


