import axios from 'axios';

let baseUrl = "https://api.themoviedb.org/3";
let apiKey = "9084eae9f770e006ebcba95dbd474e28";
let helpers = {

	getNowPlaying: (page) => {
		return axios.get(baseUrl + '/movie/now_playing', {
			params: {
				api_key: apiKey,
				language: 'en-US',
				page: page + 1
			}
		})
		.then(checkStatus)
	},

	getMovie: (id) => {
		return axios.get(baseUrl + '/movie/' + id, {
			params: {
				api_key: apiKey,
				language: 'en-US'
			}
		})
		.then(checkStatus)
	},

	searchMovie: (input, page) => {
		return axios.get(baseUrl + '/search/movie', {
			params: {
				api_key: apiKey,
				language: 'en-US',
				query: input,
				page: page + 1
			}
		})
		.then(checkStatus)
	}
}

function checkStatus(response) {
  if (response.status >= 200 && response.status < 300) {
    return response
  } else {
    var error = new Error(response.statusText)
    error.response = response
    throw error
  }
}

export default helpers;


